.container {
    background-color: #1b1b1b;
    height: calc(100vh - 40px);
    padding: 20px;
    position: relative;
}
.mainText {
    color: #ffffff;
    font-size: 32px;
    font-weight: 700;
    line-height: 38px;
    margin-top: 20px;
}
.subText {
    display: block;
    margin-top: 16px;
    font-size: 12px;
    font-weight: 500;
    line-height: 14px;
    color: #ffffff80;
}
