.mainBlock {
    background-color: #1b1b1b;
    min-height: calc(100vh - 50px);
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 50px;
}
.footerBlock {
    margin-top: 50px;
    display: flex;
    justify-content: center;
    gap: 7px;
}
.footerBlock_text {
    display: block;
    width: 164px;
    font-size: 14px;
    font-weight: 500;
    line-height: 16.8px;
    text-decoration: underline;
    color: white;
    text-align: center;
}
